import { useTheme } from '../context/ThemeContext';

const useUtils = () => {
  const { palette } = useTheme();

  const assetImg = (title) => {
    let img =
      title && palette.name === 'light'
        ? `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons/${title.substring(
            1
          )}.png`
        : `https://aperture-image-assets.s3.us-west-2.amazonaws.com/masset_icons_dark/${title.substring(
            1
          )}.png`;
    return img;
  };

  const numberParse = (num, type) => {
    switch (type) {
      case 'big':
        return Number((Number(num) * 1e6).toFixed(2));
      case 'invest':
        return (Number(num) * 1e6).toFixed(0).toString();
      case 'small':
        return Number((Number(num) / 1e6).toFixed(2));
      case 'currency':
        return `$${Number(Number(num).toFixed(2)).toLocaleString()}`;
      case 'table':
        return Number(Number(num).toFixed(0)).toLocaleString();
      default:
        return Number(Number(num).toFixed(2)).toLocaleString();
    }
  };

  const decimalFix = (num) => {
    let number = Number(num);
    if (number % 1 === 0) {
      return Math.floor(number * 10) / 10;
    } else {
      return Math.floor(number * 10000) / 10000;
    }
  };

  return {
    assetImg,
    numberParse,
    decimalFix,
  };
};

export default useUtils;
