import { useApp } from '../../context/AppContext';
import { useGas } from '../../context/GasContext';
import textContext from '../../textContext';
import AssetBreakdown from '../Investment/AssetBreakdown';

// Material
import Slider from '@mui/material/Slider';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function InvestAccordion(props) {
  const {
    plus,
    title,
    breakdown,
    defaultCol,
    collateral,
    deltaFeeInit,
    minCollateral,
    anchorFeeInit,
    withdrawValue,
    handleCollateral,
  } = props;
  const { width, language } = useApp();
  const { selectGas, gasFeeName } = useGas();
  const text = textContext[language].assets;
  const {
    feeText,
    gasText,
    strategyFeeText,
    targetText,
    reminder1,
    reminder2,
    reminder3,
    reminder4,
  } = text;

  const increaseRatio = (collateral / 100 / minCollateral - 1) * 100;
  const color =
    increaseRatio < 50
      ? { color: '#FF4242' }
      : increaseRatio < 80
      ? { color: '#F48E00' }
      : { color: '#00CA9A' };

  return (
    <Accordion className="feesClass">
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="fees-header"
        id="fees-header"
        className={!plus ? 'hiddenPlus' : ''}
      >
        <p className="title">
          {!plus && !withdrawValue ? (
            <span>
              {gasText}: {anchorFeeInit}{' '}
              <span style={{ textTransform: 'uppercase' }}>
                {gasFeeName(selectGas.name)}
              </span>
            </span>
          ) : (
            feeText
          )}
        </p>
      </AccordionSummary>
      {plus && (
        <AccordionDetails className="feesDetails">
          <p>
            <span className="colorGray">{gasText}</span>:{' '}
            {plus &&
              !withdrawValue &&
              (deltaFeeInit === 0 ? '- -' : deltaFeeInit)}{' '}
            {gasFeeName(selectGas.name)}
          </p>
          <p className="space">
            <span className="colorGray">{strategyFeeText}</span>: 10%
          </p>
          <p className="space target">
            <span className="colorGray">{targetText}</span>
            <span>{width > 1199 ? ':' : ''}</span>
            <Slider
              sx={color}
              className="slider"
              onChange={handleCollateral}
              aria-label="collateral"
              value={collateral}
              valueLabelDisplay="auto"
              step={1}
              min={Number(minCollateral) * 100 + 50}
              max={Number(minCollateral) * 100 + 250}
            />
            {collateral}%
          </p>
          <p className="space">
            <span className="colorGray">{reminder1}</span>
            {title}
            <span className="colorGray">{reminder2}</span>
            <span style={color}>{increaseRatio.toFixed(0)}%</span>
            <span className="colorGray">{reminder3}</span>
            {defaultCol}%<span className="colorGray">{reminder4}</span>
          </p>
          <AssetBreakdown
            breakdown={breakdown}
            collateralRatio={collateral / 100}
          />
        </AccordionDetails>
      )}
    </Accordion>
  );
}
