import textContext from '../../textContext';
import { useApp } from '../../context/AppContext';
import { useToken } from '../../context/TokenContext';
import useUtils from '../../hooks/useUtils';

export default function Value(props) {
  const { language } = useApp();
  const { exchange } = useToken();
  const { numberParse } = useUtils();
  const text = textContext[language].portfolio.asset;

  const histoicalCalc = () => {
    const { value, initial } = props;
    const calc = numberParse(value - initial);
    const percent = numberParse(((value - initial) / initial) * 100);
    return { calc, percent };
  };

  return (
    <div
      className="valueClass"
      style={
        props.anchor
          ? {
              justifyContent: 'flex-start',
              marginLeft: '1.75rem',
            }
          : {}
      }
    >
      <div>
        <span>{text.value}</span>
        {props.anchor && exchange ? (
          <h2>
            {numberParse(
              numberParse(props.value, 'small') * exchange['exchange_rate'],
              'currency'
            )}
          </h2>
        ) : (
          <h2>{props.value && numberParse(props.value, 'currency')}</h2>
        )}
      </div>
      {props.position !== 'anc' && <hr />}
      {props.position !== 'anc' && (
        <div>
          <span>{text.original}</span>
          <h3>{props.initial && numberParse(props.initial, 'currency')}</h3>
          <span>{text.historical}</span>
          <h3>
            {props.value &&
              props.initial &&
              `$${histoicalCalc().calc}`}{' '}
            <span className="fontOrange">
              {props.value && props.initial && histoicalCalc().percent}%
            </span>
          </h3>
        </div>
      )}
    </div>
  );
}
