import * as React from 'react';
import Tooltip from '@mui/material/Tooltip';

type Props = {
  title: JSX.Element;
  children: JSX.Element;
  props: any[];
};

const TableAssetsTooltip: React.FC<Props> = ({ children, title, ...props }) => {
  return (
    <Tooltip
      {...props}
      componentsProps={{
        tooltip: { sx: { opacity: 1, border: 1, borderColor: 'borderColor' } },
      }}
      title={<div className="tableAssetsTooltip">{title}</div>}
    >
      {children}
    </Tooltip>
  );
};

export default TableAssetsTooltip;
