import React from 'react';
import { useApp } from './AppContext';
import { useGas } from './GasContext';
import useUtils from '../hooks/useUtils';
import { useToken } from './TokenContext';
import { useQueries } from './QueriesContext';
import { useWallet } from '@terra-money/wallet-provider';
import { bombayAddress, columbiaAddress } from '../contracts/tokenAddress';
import {
  terraManagerAddress,
  swapAddress,
} from '../contracts/contractAddresses';

const DeltaContext = React.createContext();

export function useDelta() {
  return React.useContext(DeltaContext);
}

const DeltaProvider = ({ children }) => {
  const { coins } = useGas();
  const { exchange } = useToken();
  const { numberParse } = useUtils();
  const { status, network } = useWallet();
  const { holderAddress, balance } = useApp();
  const [coinSum, setCoinSum] = React.useState();
  const [deltaSum, setDeltaSum] = React.useState();
  const [holdings, setHoldings] = React.useState([]);
  const [balances, setBalances] = React.useState([]);
  const [posManager, setPosManager] = React.useState();
  const [balanceSum, setBalanceSum] = React.useState();
  const [initialSum, setInitialSum] = React.useState();
  const [positionInfo, setPositionInfo] = React.useState([]);
  const [coinBalances, setCoinBalances] = React.useState([]);
  const [deltaPositions, setDeltaPositions] = React.useState([]);
  const [anchorInvestment, setAnchorInvestment] = React.useState([]);
  const { contractNetwork, lcd, addressProvider, userStatus } = useQueries();

  const positionsByHolder = (address) => {
    let query = {
      get_terra_positions_by_holder: {
        holder: `${address}`,
        limit: 20,
      },
    };
    return query;
  };

  const getDeltaPositions = () => {
    if (network && holderAddress && userStatus.connected) {
      const getPositions = () => {
        async function getInfo() {
          let aTerra = await lcd.wasm.contractQuery(addressProvider.aTerra(), {
            balance: {
              address: holderAddress,
            },
          });
          let positions = await lcd.wasm.contractQuery(
            terraManagerAddress[contractNetwork],
            positionsByHolder(holderAddress)
          );
          let position_manager = await lcd.wasm.contractQuery(
            terraManagerAddress[contractNetwork],
            {
              get_strategy_metadata: {
                strategy_id: '0',
              },
            }
          );
          return { positions, position_manager, aTerra };
        }
        getInfo().then((data) => {
          const allPositions = [];
          const getNextPositions = async (pos, max, prevPositions) => {
            if (pos.position_id_vec.length > 0) {
              let lastPosition =
                pos.position_id_vec[pos.position_id_vec.length - 1];
              let nextPositions = await lcd.wasm.contractQuery(
                terraManagerAddress[contractNetwork],
                {
                  get_terra_positions_by_holder: {
                    holder: holderAddress,
                    start_after: `${lastPosition}`,
                    limit: max,
                  },
                }
              );
              allPositions.push(...pos.position_id_vec);
              getNextPositions(nextPositions, max, allPositions);
            } else {
              allPositions.length === 0 &&
                allPositions.push(...pos.position_id_vec);
              return setDeltaPositions(allPositions);
            }
          };
          getNextPositions(data.positions, 20);
          setAnchorInvestment({ value: data.aTerra.balance });
          setPosManager(data.position_manager.manager_addr);
        });
      };
      getPositions();
    }
  };

  const getPositionInfo = (pos) => {
    if (posManager && userStatus.connected) {
      async function getPosInfo() {
        let positionAddress = await lcd.wasm.contractQuery(posManager, {
          get_position_contract_addr: {
            position: {
              chain_id: 3,
              position_id: pos,
            },
          },
        });
        let positonInfo = await lcd.wasm.contractQuery(positionAddress, {
          get_position_info: {},
        });
        return positonInfo;
      }
      return getPosInfo();
    }
  };

  const refreshPositions = () => {
    setDeltaPositions([]);
    setPositionInfo([]);
    getDeltaPositions();
  };

  const clearBalances = () => {
    setBalances([]);
    setCoinBalances([]);
  };

  React.useEffect(() => {
    if (holderAddress) {
      const tokenAddress =
        network.chainID === 'bombay-12' ? bombayAddress : columbiaAddress;
      let token = [];
      let getBalance = async (add) => {
        let name = await lcd.wasm.contractQuery(add.address, {
          balance: { address: holderAddress },
        });
        return {
          token: add.token,
          balance: name.balance,
          address: add.address,
        };
      };
      tokenAddress.forEach((add, i) => {
        getBalance(add).then((d) => {
          token.push(d);
          if (i === tokenAddress.length - 1) {
            setHoldings(token);
          }
        });
      });
    }
  }, [network, holderAddress]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (balance && holdings.length !== 0 && balances.length === 0 && exchange) {
      let convertAUST = async (add) => {
        let aTerra = await lcd.wasm.contractQuery(addressProvider.aTerra(), {
          balance: { address: holderAddress },
        });
        return {
          token: add.token,
          amount: (
            Number(aTerra.balance) * Number(exchange.exchange_rate)
          ).toFixed(0),
          address: add.address,
        };
      };
      let convertBalance = async (add) => {
        let amount = await lcd.wasm.contractQuery(
          swapAddress[contractNetwork],
          {
            simulate_swap_operations: {
              offer_amount: add.balance,
              operations: [
                {
                  terra_swap: {
                    offer_asset_info: { token: { contract_addr: add.address } },
                    ask_asset_info: { native_token: { denom: 'uusd' } },
                  },
                },
              ],
            },
          }
        );
        return {
          token: add.token,
          amount: amount.amount,
          address: add.address,
        };
      };
      setBalances((balances) => [
        ...balances,
        { token: 'ust', amount: numberParse(balance, 'small') },
      ]);
      holdings.forEach((add, i) => {
        add.token === 'aust'
          ? convertAUST(add).then((d) => {
              setBalances((balances) => [...balances, d]);
            })
          : convertBalance(add).then((d) => {
              setBalances((balances) => [...balances, d]);
            });
      });
    }
  }, [holdings, balance, exchange]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (coinBalances.length === 0 && coins) {
      let convertCoins = async (c) => {
        let amount = await lcd.wasm.contractQuery(
          swapAddress[contractNetwork],
          {
            simulate_swap_operations: {
              offer_amount: c.amount,
              operations: [
                {
                  native_swap: {
                    offer_denom: c.denom,
                    ask_denom: 'uusd',
                  },
                },
              ],
            },
          }
        );
        return { token: c.denom, amount: amount.amount };
      };

      let coinBalance = [];
      let newCoins = coins.filter((f) => f.denom === 'uluna');
      newCoins.forEach((c, i) => {
        convertCoins(c).then((d) => {
          coinBalance.push(d);
          setCoinBalances(coinBalance);
        });
      });
    }
  }, [coins, refreshPositions]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (network && holderAddress && status && !!lcd ) {
      getDeltaPositions();
    }
  }, [network, holderAddress, status, lcd]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (balances.length === 5 && coinBalances.length <= 2) {
      let sumArr = (arr) => {
        let sum = 0;
        for (let i = 0; i < arr.length; i++) {
          sum += Number(arr[i].amount);
        }
        return numberParse(sum, 'small');
      };
      setBalanceSum(sumArr(balances));
      setCoinSum(sumArr(coinBalances));
    }
  }, [balances, coinBalances]); // eslint-disable-line react-hooks/exhaustive-deps

  React.useEffect(() => {
    if (deltaPositions.length !== 0) {
      let positionArr = [];
      let sum = 0;
      let initial = 0;
      let getAddress = async (add) => {
        let name = await lcd.wasm.contractInfo(add);
        return name;
      };
      deltaPositions.forEach((f, i) => {
        getPositionInfo(f) &&
          getPositionInfo(f).then((d) => {
            let obj = {};
            getAddress(d.mirror_asset_cw20_addr).then((res) => {
              obj.name = res.init_msg.symbol;
            });
            obj.value = d.detailed_info
              ? numberParse(d.detailed_info.uusd_value, 'small')
              : numberParse(d.position_close_info.uusd_amount, 'small');
            obj.initial = numberParse(
              d.position_open_info.uusd_amount,
              'small'
            );
            obj.time = d.position_open_info.time_nanoseconds;
            obj.address = d.mirror_asset_cw20_addr;
            obj.position = f;
            obj.closed = d.position_close_info ? true : false;
            positionArr.push(obj);
            sum = d.position_close_info ? sum : sum + Number(obj.value);
            initial = d.position_close_info
              ? initial
              : initial + Number(obj.initial);
            setDeltaSum(sum);
            setInitialSum(initial);
            if (i + 1 === deltaPositions.length) {
              setPositionInfo(positionArr);
            }
          });
      });
    }
  }, [posManager, network, deltaPositions]); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <DeltaContext.Provider
      value={{
        coinSum,
        deltaSum,
        balances,
        initialSum,
        balanceSum,
        posManager,
        coinBalances,
        positionInfo,
        clearBalances,
        getPositionInfo,
        refreshPositions,
        anchorInvestment,
      }}
    >
      {children}
    </DeltaContext.Provider>
  );
};

export { DeltaProvider, DeltaContext };
