import React from 'react';
import { useDelta } from '../../context/DeltaContext';
import * as AWS from 'aws-sdk';
import { useWallet } from '@terra-money/wallet-provider';

// Comps
import Assets from './Assets';
import Value from './Value';
import Charts from './Charts';

export default function SelectedInvest(props) {
  const [data, setData] = React.useState([]);
  const { positionInfo, anchorInvestment } = useDelta();
  const { network } = useWallet();

  React.useEffect(() => {
    const documentClient = new AWS.DynamoDB.DocumentClient({
      region: 'us-west-2',
    });
    const query = async () => {
      const response = await documentClient
        .query({
          TableName:
            network.chainID === 'bombay-12'
              ? 'position_ticks_dev'
              : 'position_ticks',
          KeyConditionExpression: 'position_id = :position_id',
          ExpressionAttributeValues: {
            ':position_id': Number(props.position),
          },
        })
        .promise();
      setData(response);
    };
    props.position !== 'anc' &&
      query().catch((error) => console.error(JSON.stringify(error, null, 2)));
    return () => {
      setData([]);
    };
  }, [network, props.position]);

  return (
    <div className="wrapper">
      {props.position === 'anc' ? (
        <React.Fragment>
          <Assets
            position={props.position}
            name={props.name}
            anchor={props.position}
            time={props.time}
            address={props.address}
            initial={props.initial}
            value={anchorInvestment.value}
          />
          <hr />
          <Value
            anchor={props.position}
            position={props.position}
            initial={props.initial}
            value={anchorInvestment.value}
          />
          <hr />
          <Charts data={data} position={props.position} />
        </React.Fragment>
      ) : (
        positionInfo
          .filter((f) => f.position === props.position)
          .map((pos, i) => (
            <React.Fragment key={i}>
              <Assets
                position={pos.position}
                name={pos.name}
                anchor={props.position.value}
                time={pos.time}
                address={pos.address}
                initial={pos.initial}
                value={pos.value}
              />
              <hr />
              <Value
                anchor={props.position.value}
                position={pos.position}
                initial={pos.initial}
                value={pos.value}
              />
              <hr />
              <Charts info={positionInfo} data={data} position={pos.position} />
            </React.Fragment>
          ))
      )}
    </div>
  );
}
