import * as React from 'react';
import { ThemeContext } from 'styled-components';
import { createTheme } from '@mui/material/styles';

export function useTheme() {
  return React.useContext(ThemeContext);
}

const ThemeProvider = ({ children, theme }) => {
  let colors = theme.colors;
  const { light } = colors;

  const [palette, setPalette] = React.useState(light);
  const muiTheme = createTheme();

  // Create default MUI theme.
  // @Katie TODO: We should consolidate our custom theming with muiTheme in the future
  // React.useEffect(() => {
  //   setMuiTheme(createTheme());
  // }, []);

  React.useEffect(() => {
    // Check local storage on page load to get last saved thme
    const themeCheck = localStorage.getItem('palette');
    if (
      themeCheck !== null &&
      (themeCheck === 'light' || themeCheck === 'dark')
    ) {
      setPalette(colors[themeCheck]);
    }
    // Set default theme
    else {
      setPalette(palette);
    }
  }, [colors, palette]);

  // Theme update function
  const themeUpdate = (t) => {
    // Update state of tguage in application and local storage
    setPalette(colors[t]);
    localStorage.setItem('palette', t);
  };

  // Color switch based on theme palette
  const themeSelect = (t) => {
    switch (t) {
      case 'light':
        themeUpdate('light');
        break;
      case 'dark':
        themeUpdate('dark');
        break;
      default:
        themeUpdate('light');
    }
  };

  return (
    <ThemeContext.Provider
      value={{
        palette,
        themeSelect,
        muiTheme,
      }}
    >
      {children}
    </ThemeContext.Provider>
  );
};

export { ThemeProvider, ThemeContext };
